<template>
  <div id="container">
    <HeaderUserBar />

    <div id="banner-content">
      <div class="banner-image">
        <div class="banner-header-bg"></div>
        <img v-if="$store.state.lang === 'ru'" src="../files/banners/ru/loveis_content.webp" alt="Slider Content">
        <img v-else src="../files/banners/uz/loveis_content.webp" alt="Slider Content">
      </div>
      <input
          type="file"
          @change="onFileChange"
          ref="fileInput"
          style="display: none"
          accept=".jpg, .png, .jpeg, |image/*"
      >
      <div id="upload-scan" @click.prevent="$refs.fileInput.click()">
        <ion-icon name="camera"></ion-icon>
        {{ $store.state.lang === 'ru' ? 'Загрузить сканер' : 'Skanerni yuklash' }}
      </div>
      <div class="desc">{{ $store.state.lang === 'ru' ? 'Главный партнер этого месяца' : 'Bu oyning asosiy hamkori' }}</div>
    </div>
  </div>
</template>

<script>
import { Cropper } from "vue-advanced-cropper";
import HeaderUserBar from "@/components/HeaderUserBar";
import axios from "axios";
import store from "@/store";
export default {
  name: "SliderContent",
  components: {
    HeaderUserBar,
    Cropper,
  },
  data: () => ({
    image: null,
    openUpload: false,
    croppedImage: null,
  }),
  mounted() {
    document.title = 'Love is';
    Telegram.WebApp.BackButton.show()
    Telegram.WebApp.BackButton.onClick(() => {
      this.$router.push('/')
    });
  },
  methods: {
    onFileChange(e) {
      const file = e.target.files[0]
      this.image = URL.createObjectURL(file)
      this.openUpload = true
      this.addGIftTickets()
    },
    async addGIftTickets() {
      await axios.post(`${store.state.API_URL}/gift/loveis/${Telegram.WebApp.initDataUnsafe.user.id}`).then(res => {
        if (this.$store.state.lang === 'ru') {
          Telegram.WebApp.showPopup({
            'title': '✅ Успешно',
            'message': 'Классическую добавлен 20 текетов',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Продолжить"
              }
            ]
          })
        } else {
          Telegram.WebApp.showPopup({
            'title': '✅ Muvofaqiyatli',
            'message': 'Sizning hisobingizga 20 ticket qo\'shildi',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Davom etish"
              }
            ]
          })
        }
        Telegram.WebApp.onEvent('success', () => Telegram.WebApp.close())
        Telegram.WebApp.HapticFeedback.notificationOccurred('success')
      }).catch(e => {
        if (this.$store.state.lang === 'ru') {
          Telegram.WebApp.showPopup({
            'title': '⚠️Ошибка!',
            'message': 'Вам уже добавлена подарочные тикеты',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Повторить"
              }
            ]
          })
        } else {
          Telegram.WebApp.showPopup({
            'title': '⚠️Xato!',
            'message': 'Sizga sovg\'a ticketlar qoshilgan',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Davom etish"
              }
            ]
          })
        }
        Telegram.WebApp.HapticFeedback.notificationOccurred('error')
      })
    }
  }
}
</script>

<style scoped>
@import url('../style/slider-content.css');
</style>
